.banner{
  color: $color-white;
  padding-top: 80px;
 background-size: cover;
}
.banner__title{
  text-transform: uppercase;
  font-weight: 500;
  padding-top: 50px;
}
.banner__info{
  font-size: $font-general;
  font-weight: 400;
  padding:35px 0 95px;
}
@media #{$xs-layout}{
  .banner{
    text-align: center;
    height: 100vh;
  }
  .banner-container{
    height: 100%;
    @include flexbox;
    margin: auto;
    align-items: center;
  }
  .banner__title{
    font-size: 30px;
    line-height: 1.2;
  }
}