.header {
  width: 100%;
  color: $color-white;
  padding: 30px 0;
}
.header_style-1{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}
.header_scrolled{
  background-color: #191919;
  box-shadow: 0 1px 5px rgba(0,0,0,0.1);
  z-index: 1;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}
.nav__row{
  align-items: center;
}
.header__nav{
  margin: 0 auto;
}
.header__nav_item{
  display: inline-block;
  padding: 0 20px;
}
.header__nav_item .js-link:hover{
  color: $color-green;
}
.header__logo{
  position: absolute;
}
@media #{$xs-layout} {
  .header{
    padding: 15px 0;
  }
  .header__nav_item{
    padding: 0 13px;
  }
  .header_style-mobile{
    background-color: #191919;
  }
  .header__logo{
    img{
      width: 70%;
    }
  }
}