//general styles
.rating {
  font-family: "FontAwesome";
  position: relative;
  border: none;
  margin: 0 auto;
}
.star-rating{
  display: inline-block;
  position: relative;
}
.star{
  font-style: normal;
  color: $color-white;
}
.star-half {
  display:none;
  color: $color-star;
  position: absolute;
  top: 0;
  left: 0;
}
.star-filled{
  color: $color-star;
}
// dynamic stars
.rating > input {
  display: none;
}
.rating > label:before {
  font-size: 22px;
  display: inline-block;
  content: "\f005";
}
.rating > .half:before {
  content: "\f089";
  position: absolute;
}
.rating > label {
  color: $color-white;
  float: right;
}

/* Stars on Hover */
.rating > input:checked ~ label, /* show gold star when clicked */
.rating:not(:checked) > label:hover, /* hover current star */
.rating:not(:checked) > label:hover ~ label { color: #ffcc00;  } /* hover previous stars in list */
.rating > input:checked + label:hover, /* hover current star when changing rating */
.rating > input:checked ~ label:hover,
.rating > label:hover ~ input:checked ~ label, /* lighten current selection */
.rating > input:checked ~ label:hover ~ label { color: #FFED85;  }

.table-star{
  font-size: 16px;
  color: $color-grey;
}

// rating table stars
.table-star-rating:not(:last-child){
  padding-right: 3px;
}
// top-section stars
.top-star{
  font-size: 18px;
}
.top-star-rating:not(:last-child){
  padding-right: 5px;
}
