.section__article_col{
  padding-top: 40px;
}
.article__content{
  line-height: 2;
  padding-bottom: 120px;
}
.article__image{
  display:block;
  height: 280px;
  background-color: #e8e8e8;
}
.article__time{
  font-size: $font-section-title;
  color: $color-dark-grey;
  padding-top: 25px;
}
.article__title{
  font-size: 30px;
  font-weight: 500;
  line-height: 1.5;
}
.article__text{
  padding-top: 20px;
}
.article__share{
  padding-bottom: 70px;
}
.social__share_text{
  color: $color-dark-grey;
  font-size: 16px;
}
@media #{$sm-layout}{
  .section__article_col {
    padding-top: 20px;
  }
  .article__content {
    padding-bottom: 70px;
  }
  .article__share{
    padding-bottom: 0;
  }
  .article__time {
    font-size: 16px;
    color: #919191;
    padding-top: 15px;
  }
  .article__text {
    padding-top: 10px;
  }
}