.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0,0,0,0.4);
}
.modal-content {
  background-color: $color-light-grey;
  margin: 7% auto 0;
  padding: 50px 67px 40px;
  width: 36.95%;
  position: relative;
}

.modal__container{
  margin: 0 auto;
}
.modal__title{
  padding-bottom: 35px;
  h3{
    font-size: 24px;
    font-weight: 500;
  }
}
.modal-open{
  overflow: hidden;
}
.form-group{
  margin-bottom: 15px;
}
.form-control{
  width: 100%;
  padding: 14px 15px;
  border: 1px solid $color-grey;
  -webkit-box-shadow: inset 0px 1px 0px 1px rgba(190,190,190,1);
  -moz-box-shadow: inset 0px 1px 0px 1px rgba(190,190,190,1);
  box-shadow: inset 0px 1px 0px 1px rgba(190,190,190,1);
  @include placeholder{
    font-family: "Rubik";
    color: #bebebe;
    font-style: italic;
    font-size: $font-general;
    font-weight: 300;
  }
}
.form-textarea{
  min-height: 130px;
}
.modal__rating{
  padding: 23px 0 45px;
}
.modal__rating_title{
  font-weight: 400;
  font-size: $font-content-title;
  padding-bottom: 10px;
}
.modal__rating_stars{
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 195px;
  label:before{
    padding: 0 9px;
  }
}
.form-checkbox{
  padding-top: 70px;
  text-align: left;
}
.modal-checkbox {
  vertical-align: top;
  margin-bottom: 60px;
  display: block;
  float: left;
}
.modal-checkbox-label{
  font-size: $font-xxs;
  font-weight: 300;
  color: #919191;
  vertical-align: top;
}
.modal-close{
  display: none;
  border: none;
  background: none;
  position: absolute;
  top: 10px;
  right: 5px;
  font-size: 14px;
  outline: none;
  cursor: pointer;
}
@media #{$xlg-layout} {
  .modal-content{
    width: 50%;
  }
}
@media #{$sm-layout} {
  .modal-content {
    width: 70%;
    margin: 7% auto 0;
    padding: 20px 15px 20px;
  }
}
@media #{$xs-layout}{
  .modal-close{
    display: block;
  }
  .modal-content{
    width: 100%;
    margin: 0;
    height: 100vh;
  }
  .modal__title {
    padding-bottom: 15px;
  }
  .modal__rating {
    padding: 10px 0 20px;
  }
}