.btn{
  display: inline-block;
  text-decoration: none;
  outline: none;
  border: none;
  cursor: pointer;
  line-height: 1;
}
.btn-round{
  -webkit-border-radius: 32px;
  -moz-border-radius: 32px;
  border-radius: 32px;
  text-align: center;
  font-weight: 500;
  @include transition(all 0.3s ease-in-out 0s);
  &:hover{
    transform: translateY(-3px);
  }
  &:active{
    -webkit-transform: translate(0, 3px);
  }
}
.btn-lg{
  padding: 23px 28px;
  font-size: $font-content-title-md;
  background-color: $color-green;
  color: $color-white;
}
.btn-md{
  padding: 8px 20px;
  font-size: $font-sm;
  border: 1px solid $color-green;
  color: $color-green;
}
.btn-sm{
  font-size: $font-content-title;
  color: $color-green;
}
.btn-lg:hover{
  @include box-shadow(0px 5px 28px -4px #a6a6a6);
}
.btn-md:hover{
  border-color: $color-light-green;
}
.btn-text:hover{
  color: #00aa78;
}
.btn-previous{
  font-size: 16px;
  color: $color-green;
}
.modal-btn{
  display: inline-block;
  font-size: 16px;
  padding: 15px 28px;
  width: 225px;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
.slick-btn{
  font-size: $font-content-title;
  background: none;
  color: $color-green;
  border: none;
  outline: none;
  cursor: pointer;
}
.btn-mobile{
  display: none;
}
@media #{$lg-layout}{
  .btn-mobile{
    display: block;
    text-align: center;
  }
  .btn-reviews-mobile{
    margin: 30px 0 40px;
  }
}
@media #{$xs-layout}{
  .btn-sm{
    font-size:$font-general;
  }
  .text-block{
    display: inline;
  }
}