.pagination {
  padding: 10px 0 70px; }

.pagination ul {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;}

.pagination ul li:first-child {
  margin-left: 0; }

.pagination ul li {
  display: inline-block;
  vertical-align: middle;
  margin: 0 15px; }

.pagination ul li a {
  display: inline-block;
  vertical-align: middle; }

.pagination ul li a:hover {
  color: $color-green;}

.pagination ul li a:focus {
  color: $color-green; }