.section__articles{
  background-color: $color-grey;
  position: relative;
  padding-bottom: 110px;
}
.section__articles_row{
  overflow:hidden;
}
.section__articles_col{
  float: left;
  background-color: $color-white;
  outline: none;
  border: none;
  margin-bottom: 15px;
}
.section__articles_content{
  padding: 25px 10px;
}
.article__col_img{
  img{
    width:100%;
  }
}
.article__col_text{
  padding: 23px 0 27px;
  line-height: 22px;
  font-weight: 500;
}
.article__col_time{
  color: $color-grey;
  font-size: $font-sm;
}
.slick-prev-article{
  position: absolute;
  bottom: 65px;
  right: 50%;
  padding-right: 30px;
}
.slick-next-article{
  position: absolute;
  bottom: 65px;
  left: 50%;
  padding-left: 30px;
}
@media  #{$xlg-layout}{
  .article__col_text {
    padding: 16px 0 20px;
  }
}
@media #{$xs-layout}{
  .section__articles{
    padding-bottom: 90px;
    .container{
      padding-left: 0;
      padding-right: 0;
    }
  }
  .section__articles_col{
    width: 14.2% !important;
    padding: 15px;
    min-height: 130px;
    position: relative;
  }
  .slick-track{
    .col{
      margin: 0 0 8px;
    }
  }
  .article__col_img{
    display: inline-block;
    width: 30%;
  }
  .section__articles_content{
    display: inline-block;
    float: right;
    width: 60%;
    padding: 0;
  }
  .article__col_time{
    position: absolute;
    bottom: 20px;
  }
  .article__col_btn{
    float: right;
    position: absolute;
    bottom: 17px;
    right: 15px;
  }
  .article__col_text{
    padding: 0 0 15px;
    line-height: 1.3;
  }
  .slick-prev-article{
    bottom: 45px;
  }
  .slick-next-article{
    bottom: 45px;
  }
}
