@include font-face($font-map, $font-url);

*, ::after, ::before {
  box-sizing: border-box;
}
textarea{
  outline: none;
}
a {
  color: inherit;
  text-decoration: none;
}
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
body {
  line-height: 1.8;
  font-size: $font-general;
}
.text-block{
  display: block;
}
.text-center{
  text-align: center;
}
.col{
  margin: 0 15px;
  padding: 0;
}
.js-link{
  color: $color-white;
}
body{
  font-family: $font-main;
  font-size: $font-general;
}
.section{
  padding: 60px 0;
}
.section__content{
  text-align: center;
  padding-bottom: 40px;
}
.section_title{
  font-size: $font-section-title;
  font-weight: 500;
  line-height: 1.5;
}
.search-input{
  width: 48%;
  border: 1px solid $color-grey;
  padding: 9px 12px;
  @include placeholder {
    font-size: $font-general;
    font-style: italic;
  }
}
@media #{$sm-layout}{
  body{
    line-height: 1.5;
  }
  .section{
    padding: 20px 0;
  }
}
@media #{$xs-layout}{
  body{
    font-size: 12px;
  }
  .section-mobile{
    margin-top: 50px;
  }
  .section__content{
    padding-bottom: 25px;
  }
  .section_title{
    line-height: 1.2;
  }
  .search-input {
    width: 100%;
  }
  .block-mb-hide{
    display: none;
  }
  .icon-right{
    padding-left: 10px;
  }
  .icon-left {
    padding-right: 10px;
  }
  .text-block{
    display: inline;
  }
}