.section__top{
  background-color: $color-grey;
}
.section__top_title{
  padding-top: 15px;
}
.section__top_col{
  width: 16.9%;
  background-color: $color-white;
}
.section__top_col_container{
  width: 86%;
  margin:0 auto;
  padding-bottom: 70px;
}
.top__col{
  margin: 0 15px;
  padding: 0;
}
.top__col_number{
  padding: 35px 0 10px;
  font-weight: 500;
  font-size: $font-title;
  color: $color-green;
}
.top__col_company-name{
  font-weight: 500;
  font-style: italic;
  font-size: $font-content-title-md;
  padding: 10px 0 20px;
}
.top__col_company-rate{
  background-color: $color-green;
  padding: 20px 0;
}
@media #{$xlg-layout}{
  .section__top_col_container {
    width: 100%;
  }
  .section__top_col {
    width: 16.8%;
  }
}
@media #{$sm-layout} {
  .section__top_col_container{
    width: 50%;
    padding-bottom: 0;
  }
  .section__top{
    padding-top: 40px;
  }
  .section__top_col{
    width:100%;
    margin-bottom: 40px;
  }
}
