@mixin font-face($list, $url) {
  @each $font, $font-list in $list {
    $font-family: $font;

    @each $font-weight, $font-weight-number in $font-list {
      @font-face {
        font-family: "#{$font-family}";
        src: url('#{$url}/#{$font-family}-#{$font-weight}.woff') format('woff'),
        url('#{$url}/#{$font-family}-#{$font-weight}.ttf') format('truetype'),
        url('#{$url}/#{$font-family}-#{$font-weight}.otf') format('truetype');
        font-weight: $font-weight-number;
        font-style: normal;
      }
    }
  }

}