.company__filter{
  border: 1px solid $color-green;
  padding-top: 25px;
  padding-bottom: 40px;
}
.company__filter_list{
  padding: 0 40px;
  margin-right: 40px;
  height: 255px;
  overflow-y: scroll;

}
.filter__list_title{
  font-size:$font-content-title;
  margin-bottom: 16px;
  font-weight: 400;
  padding-left: 30px;

}
.filter__list_item{
  padding-top: 9.5px;
}
.filter__company{
  color:#919191;
  padding-left: 10px;
  &:hover{
    cursor: pointer;
  }
}
.filter__input{
  vertical-align: middle;
  &:hover{
    cursor: pointer;
  }
}
.control {
  position: relative;
  padding-left: 25px;
  cursor: pointer;
  &:checked{
    color: #000000;
  }
}
.control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.control_indicator {
  position: absolute;
  top: 0px;
  left: 0;
  height: 13px;
  width: 13px;
  background: #f4f3f3;
  box-shadow: inset 0px 1px 3px 0px #bebebe;
}
.control-radio .control_indicator {
  border-radius: 100%;
}
.control_indicator:after {
  box-sizing: unset;
  content: '';
  position: absolute;
  display: none;
}
.control input:checked ~ .control_indicator:after {
  display: block;
}
.control-radio .control_indicator:after {
  left: 0;
  top: 3px;
  height: 7px;
  width: 7px;
  border-radius: 50%;
  background: #00c68c;
  bottom: 0;
  right: 0;
  margin: 0 auto;
}
.company__filter_list::-webkit-scrollbar-track {border-radius: 10px;}
.company__filter_list::-webkit-scrollbar {width: 5px;}
.company__filter_list::-webkit-scrollbar-thumb {border-radius: 10px;background: #00c68c;  cursor:pointer}
.company__filter_list::-webkit-scrollbar-track-piece{background-color: #f4f3f3; border-radius: 10px;}

@media #{$xlg-layout}{
  .company__filter_list {
    padding: 0 20px;
    margin-right: 20px;
  }
}
@media #{$lg-layout}{
  .company__filter{
    display: none;
  }
}