.section__testimonials_title{
  padding: 50px 0;
}
.company__testimonials_details{
  padding-top: 25px;
  padding-bottom: 20px;
}
.company__testimonials_row{
  margin-bottom: 60px;
  padding-left: 15px;
  border-left: 1px solid $color-green;
  p{
    line-height: 2;
  }
}
.testimonials__author{
  font-weight: 500;
  font-size: 16px;
  padding-right: 110px;
}
.testimonials__time{
  color:#919191;
}
.search-mobile{
  display: none;
}
@media #{$lg-layout}{
  .search-mobile{
    display: block;
    text-align: center;
  }
  .section__testimonials_title {
    padding: 25px 0;
  }
}
@media #{$sm-layout} {
  .section__testimonials_title {
    padding: 30px 0;
    text-align: center;
  }
  .company__testimonials_row {
    margin-left: 0;
    margin-bottom: 40px;
    padding-left: 0;
    padding-bottom: 20px;
    border-bottom: 1px solid #00c68c;
    border-left: none;
  }
  .company__testimonials_details {
    padding-top: 15px;
    padding-bottom: 10px;
  }
}
