//fonts
@import "fonts";

//colors
$color-white: #fff;
$color-black: #000;
$color-dark: #191919;
$color-light-grey: #e5e5e5;
$color-grey: #e8e8e8;
$color-dark-grey: #919191;
$color-green: #00c68c;
$color-light-green: #a0ead3;
$color-red: #ff0f0f;
$color-link: #05153f;
$color-star: #ffcc00;

//font-size
$font-xxs: 9px;
$font-xs: 10px;
$font-sm: 12px;
$font-general: 14px;
$font-title: 36px;
$font-section-title: 24px;
$font-content-title: 16px;
$font-content-title-md: 18px;

// Layouts
$sp-layout : 'only screen and (min-width: 1920px)';
$xx-layout : 'only screen and (min-width: 1600px) and (max-width: 1919px)';
$xlg-layout : 'only screen and (max-width: 1200px)';
$lg-layout : 'only screen and (max-width: 992px)';
$md-layout:'only screen and (min-width: 768px) and (max-width: 991px)';
$sm-layout:'only screen and (max-width: 768px)';
$xs-layout: 'only screen and (max-width: 570px)';

