.section__reviews{
  padding-bottom: 68px;
}
.section__reviews_row{
  padding-bottom: 40px;
}
.section__reviews_col{
  margin-bottom: 15px;
}
.reviews__col_wrapper{
  border: 1px solid $color-green;
  padding: 0 28px 15px;
  min-height: 360px;
}
.reviews__col_detailes{
  font-weight: 400;
  padding: 10px 0 30px;
}
.reviews__col_user-name{
  font-size: $font-content-title;
}
.reviews__col_time{
  float: right;
  color: $color-grey;
  font-size: $font-sm;
  line-height: 1.6;
}
.reviews__col_text{
  font-weight: 300;
}
@media #{$lg-layout}{
  .reviews__col_time {
    float: none;
    display: block;
    padding-top: 10px;
  }
  .reviews__col_detailes {
    padding: 0 0 10px;
  }
  .reviews__col_wrapper {
    min-height: 440px;
  }
}
@media #{$sm-layout}{
  .reviews__col_wrapper {
    min-height: 250px;
  }
}
@media #{$xs-layout} {
  .reviews__col_wrapper {
    min-height: 300px;
  }
  .section__reviews_col{
    display: none;
  }
  .section__reviews_col:nth-child(1){
    display: block;
  }
}