.icon{
  font-family: FontAwesome;
  font-style: normal;
}
.icon-sm{
  font-size: 16px;
}
.icon-left{
  padding-right: 20px;
  display: inline-block;
}
.icon-right{
  padding-left: 20px;
  display: inline-block;
}
.icon-slick-left{
  padding-right: 20px;
  display: inline;
  vertical-align: middle;
}
.icon-slick-right{
  padding-left: 20px;
  display: inline;
  vertical-align: middle;
  float: right;
}
.arrow-icon{
  font-family: "FontAwesome";
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
}
.social-icon {
  font-family: 'FontAwesome';
  font-style: normal;
  line-height: 2;
  color: #ffffff;
  display: block;
  text-align: center;
}
.social__share_icon{
  display: inline-block;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  margin: 0 10px;
  cursor: pointer;
  &:hover{
    opacity: 0.6;
  }
}
.bg-ok{
  background-color: #f99400;
}
.bg-facebook{
  background-color: #23599b;
}
.bg-vk{
  background-color: #2e77a4;
}
.bg-twitter{
  background-color: #00a8d4;
}
.icon__favorites {
  width: 60px;
  height: 60px;
}
.icon__trophy{
  width: 60px;
  height: 70px;
}
@media #{$xs-layout} {
  .icon-right {
    padding-left: 10px;
  }
  .icon-left {
    padding-right: 10px;
  }
}