.table__rating{
  background-color: #fff;
  width: 100%;
  margin: 16px 0 34px;
  border: 1px solid $color-grey;
}
.table__header{
  background-color: $color-grey;
}
.table__header_item{
  outline: none;
  font-size: $font-content-title;
  font-weight: 500;
  vertical-align: middle;
}
.table__header_item:nth-child(1){
  width: 39.44%;
}
.table__header_item:nth-child(2){
  width: 21.4%;
}
.table__header_item:nth-child(3){
  width: 17.5%;
}
.table__header_item:nth-child(4){
  width: 22.5%;
}
.table_cell,
.table__header_item{
  height: 60px;
  line-height: 60px;
  vertical-align: middle;
}
.table_cell:first-child,
.table__header_item:first-child{
  padding-left: 10px;
}
.company_logo{
  width: 125px;
  display: inline-block;
  img{
    vertical-align: middle;
  }
}
.table_cell_link{
  color: $color-link;
  &:hover{
    text-decoration: underline;
  }
}
.table_cell_btn{
  vertical-align: middle;
}
.dataTables_empty{
  padding: 20px;
}
.dataTables_paginate{
  text-align: center;
  .previous,
  .next{
    font-family: "FontAwesome";
    color: $color-green;
    font-size: 0;
  }
  .previous:before{
    content: "\f104";
    font-size: 20px;
    font-weight: bold;
  }
  .next:after{
    content: "\f105";
    font-size: 20px;
    font-weight: bold;
  }
  .current{
    color: $color-green;
  }
  .disabled{
    color: $color-grey;
  }
}
.paginate_button{
  padding: 0 15px;
  font-size: $font-content-title-md;
  outline: none;
  cursor: pointer;
}
.dataTables_empty{
  text-align: center;
}
@media #{$xlg-layout}{
  .table__header_item:nth-child(1) {
    width: 35%;
  }
}
@media #{$sm-layout} {
  .section__rating_table {
    width: 100%;
  }
  .table__rating{
    width: 100%!important;
    margin:0 auto;
  }
  .table_cell:nth-child(1){
    width: 30%;
  }
  .table_cell:nth-child(2){
    width: 30%;
  }
  .table_cell:nth-child(3){
    width: 20%;
  }
  .table__header_item:nth-child(4) {
    display: none;
  }
  .table_cell_btn{
    display: none;
  }
  .company_logo {
    width: auto;
    display: block;
  }
  .table_cell {
    line-height: 1;
  }
  .table_cell, .table__header_item {
    padding: 10px 0;
  }
  .table__header_item{
    width: auto!important;
  }
  .table__header_item{
    line-height: 1;
  }
  .company_name{
    line-height: 2;
  }
}
@media #{$xs-layout}{
  .section__rating{
    .container{
      padding: 0;
    }
  }
  .dataTables_filter input {
    width: 80.7%;
    margin-bottom: 20px;
  }
  .table__header_item{
    font-size: 12px;
  }
  .table__rating{
    border: none;
  }
  .dataTables_paginate {
    margin-top: 30px;
    margin-bottom: 20px;
  }
}